/**
 * 
 *
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 *
 */

let baseUrl = "";
let routerMode = "hash";
let imgBaseUrl = "";

if (process.env.NODE_ENV == "development") {
  imgBaseUrl = "//elm.cangdu.org/img/";
  baseUrl = "//cangdu.org:8001";
} else if (process.env.NODE_ENV == "production") {
  baseUrl = "//elm.cangdu.org";
  imgBaseUrl = "//elm.cangdu.org/img/";
}

export { baseUrl, routerMode, imgBaseUrl };
